.specificPortfolioContainer {
    width: 40%;
    background-color: #282c34;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.specificPortfolioContainer p {
    margin: 0;
}

.specificPortfolioImageContainer {
    width: 100%;
    height: 70%;
    /*height: max-content*/;
    border: 1px solid;
    background-color: darkslategray;
    display: flex;
    justify-content: center;
    align-items: center;
}

.specificPortfolioImageContainer button {
    opacity: 70%;
    width: 100px;
    height: 40px;
}

.specificPortfolioProjectHeader, .specificPortfolioProjectDescription {
    margin: 10px 15%;
    border: 1px solid;
    text-align: justify;
    padding: 10px;
}

.specificPortfolioProjectHeader {
    height: 10%;
}

.specificPortfolioProjectDescription {
    font-size: 1rem;
    height: 20%;
    overflow: hidden;
}

@media screen and (max-width: 599px) {
    .specificPortfolioContainer {
        width: 90%;
        margin-bottom: 50px;
    }
    .specificPortfolioProjectHeader, .specificPortfolioProjectDescription {
        width: 80%;
        margin: 10px auto;
    }
}
